.promo {
    padding-top: 206px;
    min-height: 100vh ;
    background: url('../img/main_bg.jpg') center (center / cover) no-repeat;
    &-name {
        position: relative;
        font-weight: 700;
        font-size: 16px;
        padding-left: 10px;
        z-index: 2;
        @include before_circle;
    }
    &-title {
        width: 532px;
        font-weight: 700;
        font-size: 48px;
        line-height: 60px;
        color: #000000;
        margin-top: 9px;

    }
    .btn {
        margin-top: 25px;
        font-weight: 700;
    }

    a {
        margin-left: 24px;
        font-weight: 700;
        font-size: 14px;
        line-height: 21px;
        /* identical to box height */
        color: #000000;
        text-decoration: none;
    }
    .hamburger {
        position: absolute;
        top: 20px;
        right: 30px;
        height: 21px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        align-items: flex-end;
        cursor: pointer;
        span {
            display: block;
            width: 30px;
            height: 3px;
            background-color: #151812;
            
        }
        span.long {
            width: 36px;
        }
    }

}
